<template>
    <div>
        <el-card class="common_box">
            <h3 class="title"></h3>
            <br/>
            <el-button type="primary" @click="showAddBox()"></el-button>
            <br/>
            <br/>
            <el-table
                border
                size="small"
                stripe
                :data="tableData"
                style="width: 100%">
                <el-table-column prop="floor" label="" width="100"></el-table-column>
                <el-table-column prop="fileName" label="" width="180"></el-table-column>
                <el-table-column prop="order" label="" width="180"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="primary" @click="showFixBox(scope.row)" size="mini"></el-button>   
                        <el-button type="danger" @click="deleteFunc(scope.row)" size="mini"></el-button>   
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <el-dialog
            :title="title"
            :visible.sync="visible"
            width="40%"
            center>
            <el-form :model="form" label-width="100px">
                <el-form-item label="" prop="floor">
                    <el-input v-model="form.floor"></el-input>
                </el-form-item>
                <el-form-item label="" prop="fileName">
                    <el-input v-model="form.fileName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="geofile">
                    <input type="file" @change="fileChange" class="file" />
                </el-form-item>
                <el-form-item label="" prop="order">
                    <el-input-number v-model="form.order"></el-input-number>
                </el-form-item>
                <el-form-item label="" prop="geometry">
                    <el-select v-model="form.geometry" placeholder="">
                        <el-option label="Point" value="Point"></el-option>
                        <el-option label="LineString" value="LineString"></el-option>
                        <el-option label="Polygon" value="Polygon"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="reset"> </el-button>
                <el-button type="primary" @click="fixFunc"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { cookie } from "@/util.js";
import ajax from "@/request.js";

export default {
    data() {
        return {
            user: "",
            form: {
                floor: "",
                fileName: "",
                order: "",
                geometry: ""
            },
            title: "",
            visible: false,
            tableData:[] ,
            file: null
        };
    },
    inject: ['reload'],
    created() {
        this.user = cookie.get('user');
    },
    mounted() {
        this.getData();
    },
    methods: {
        getData() {
            ajax.post("/admin/floor/list", {}).then(res => {
                if (res.code == 200) {
                    this.tableData = res.data;
                }
            }).catch(err=> {
                console.log(err);
            });
        },
        fileChange(event) {
            var file = event.target.files[0];
            this.file = file;
        },
        showFixBox(row) {
            this.visible = true;
            // console.log(row)
            this.form = row;
            this.title = ""
        },
        showAddBox(){
            this.visible = true;
            this.title = ""
        },
        fixFunc() {
            if(!this.file){
                this.$message({
                    type: 'warning',
                    message: ''
                });
            } else {
                ajax.postFormData(
                    `/admin/floor/upload/${this.form.geometry}/${this.form.floor}/${this.form.fileName}/${this.form.order}`, {
                    geofile: this.file
                }).then((res) => {
                    if (res.code == 200) {
                        if (this.title == "") {
                            this.$message({
                                type: 'success',
                                message: ''
                            });
                        } else {
                            this.$message({
                                type: 'success',
                                message: ''
                            });
                        }
                        
                        this.reset();
                        this.getData();
                    }
                });
            }
        },
        reset() {
            this.visible = false;
            this.form = {};
        },
        deleteFunc(row) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                ajax.post(`/admin/floor/del/${row.floor}/${row.fileName}`, {}).then((res) => {
                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                    this.getData();
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            });
        }
    }
}
</script>
<style  scoped>
    .common_box{
        min-height: 300px;
    }
    .common_box .title {
        margin-bottom: 0;
    }
</style>
