<template>
    <div>
        <el-card class="common_box">
            <h3 class="title"></h3>
            <br/>
            <el-table
                border
                stripe
                :data="tableData"
                style="width: 100%">
                <el-table-column prop="floor" label="" width="80"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button class="btn_item" :key="i" v-for="(item,i) in scope.row.fileName" type="primary" @click="jump(scope.row, item)" size="small">
                            <span>{{item}}</span>
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <br/>
        </el-card>
    </div>
</template>

<script>
import { cookie } from "@/util.js";
import ajax from "@/request.js";


export default {
    data() {
        return {
            user: "",
            currentPage: 1,
            tableData:[]
        };
    },
    inject: ["reload"],
    created() {
        this.user = cookie.get('user');
    },
    mounted() {
        this.getAllFloor();
    },
    methods: {
        getAllFloor() {
            ajax.get(`/admin/properties/allFloorAndFileName`, {}).then(res => {
                if (res.code == 200) { 
                    this.tableData = res.data;
                }
            }).catch(err=> {
                console.log(err);
            });
        },
        jump(row, item) {
           // console.log(row, item)
            this.$router.push({
                path: `/mapAttrs/${row.floor}/${item}`
            })
        }
    }
}
</script>
<style  scoped>
    .common_box{
        min-height: 300px;
    }
    .common_box .el-tag{
        margin-right: 5px;
    }
    .common_box .el-tag:last-child{
        margin-right: 0;
    }
    .common_box .title {
        margin-bottom: 0;
    }
    .btn_item{
        display: inline-block;
    }
</style>
