import router from './router';
import {
    Message
} from 'element-ui';
import { cookie } from "@/util.js";

const whiteList = ['/login'];

router.beforeEach(async (to, from, next) => {
    const hasToken = cookie.get("token");
next();
return;
    if (hasToken) {
        if (to.path === '/login') {
            // 
            next({
                path: '/dashboard'
            });
        } else {
            next();
        }
    } else {
        /* token */
        if (whiteList.indexOf(to.path) !== -1) {
            // 
            next();
        } else {
            // 
            next(`/login?redirect=${to.path}`);
        }
    }
})

