<template>
    <div>
        <el-card class="common_box">
            <h3 class="title"></h3>
            <br/>
            <el-button type="primary" @click="addFunc"></el-button> 
            <br/>
            <br/>
            <el-table
                border
                stripe
                :data="tableData"
                style="width: 100%">
                <el-table-column prop="id" label="ID" width="120"></el-table-column>
                <el-table-column prop="floor" label="" width="80"></el-table-column>
                <el-table-column prop="name" label="" width="100"></el-table-column>
                <!--
                <el-table-column prop="startTime" label="" width="150"></el-table-column>
                <el-table-column prop="endTime" label="" width="150"></el-table-column>
                -->
                <el-table-column label="">
                    <template slot-scope="scope">  
                        <el-tag v-if="scope.row.state == 0" type="success"></el-tag>
                        <el-tag v-else type="warning"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">  
                        <el-button type="danger" @click="delFunc(scope.row)" size="mini"></el-button> 
                        <el-button type="primary" @click="fixFunc(scope.row)" size="mini"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <el-dialog
            :title="title"
            :visible.sync="visible"
            width="50%"
            center>
            <el-form :model="form" label-position="top" label-width="100px">
                <el-form-item label="ID">
                    <el-input v-model="form.id"></el-input>
                </el-form-item>
                <el-form-item label="">    
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="">    
                    <el-input v-model="form.floor"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="reset"> </el-button>
                <el-button type="primary" @click="doAddFunc"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { cookie } from "@/util.js";
import ajax from "@/request.js";

export default {
    data() {
        return {
            user: "",
            visible: false,
            tableData:[],
            title: "",
            form: {
                id: "",
                name: "",
                floor: ""
            }
        };
    },
    inject: ["reload"],
    created() {
        this.user = cookie.get('user');
    },
    mounted() {
        this.getData();
    },
    methods: {
        getData() {
            ajax.get("/park/list", {}).then(res => {
                if (res.code == 200) {
                    this.tableData = res.data;
                }
            }).catch(err=> {
                console.log(err);
            });
        },
        reset() {
            this.visible = false;
            this.form = {
                id: "",
                name: "",
                floor: ""
            };
        },
        addFunc() {
            this.visible = true;
        },
        doAddFunc() {
            if (!this.form.id) {
                this.$message.error("ID");
            } else if (!this.form.name) {
                this.$message.error("");
            } else if (!this.form.floor) {
                this.$message.error("");
            } else {
                if (this.title == "") {
                    ajax.post("/admin/park/add", {
                        id: this.form.id,
                        name: this.form.name,
                        floor: this.form.floor
                    }).then(res => {
                        if (res.code == 200) {
                            this.$message({
                                type: 'success',
                                message: '!'
                            });
                            this.getData();
                            this.reset();
                        }
                    }); 
                } else {
                    if (!this.form.id) {
                        this.$message.error("ID");
                    } else if (!this.form.name) {
                        this.$message.error("");
                    } else if (!this.form.floor) {
                        this.$message.error("");
                    } else {
                        ajax.post("/park/update", {
                            ...this.form
                        }).then(res => {
                            if (res.code == 200) {
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                                this.getData();
                                this.reset();
                            }
                        }); 
                    }
                }
                
            }
        },
        fixFunc(row) {
            this.form.id = row.id;
            this.form.name = row.name;
            this.form.floor = row.floor;
            this.visible = true;
            this.title = ""
        },
    
        delFunc(row) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                ajax.post("/admin/park/del/" + row.uuid, {}).then((res) => {
                    if (res.code == 200) {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                        this.getData();
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            });
        }
    }
}
</script>
<style scoped>
    .common_box{
        min-height: 300px;
    }
    .common_box .title {
        margin-bottom: 0;
    }
    .color_set{
        margin-right: 30px;
        vertical-align: middle;
    }
    .color_set .el-color-picker {
        top: 15px;
    }
    .t_wrap{
        margin-right: 10px;
    }
    .t_wrap:last-child{
        margin-right: 0;
    }
    .color_wrap{
        border: 1px solid #ddd;
        padding: 2px 3px;
        display: inline-block;
        line-height: 0;
        position: relative;
        top: 2px;
    }
    .color{
        width: 40px;
        height: 15px;
        display: inline-block;
    }
</style>
