<template>
    <div>
        <el-card class="common_box">
            <h3 class="title"></h3>
            <br/>
            <el-button type="primary" @click="addFunc"></el-button> 
            <br/>
            <br/>
            <el-table
                border
                stripe
                :data="tableData"
                style="width: 100%">
                <el-table-column prop="typeName" label="Key" width="100"></el-table-column>
                <el-table-column label="" width="400">
                    <template slot-scope="scope">  
                        <label class="t_wrap"><i class="color_wrap">
                            <span class="color" :style="'background-color:'+scope.row.typeData.fill"></span>
                        </i></label>
                        <label class="t_wrap"><i class="color_wrap">
                            <span class="color" :style="'background-color:'+scope.row.typeData.stroke"></span>
                        </i></label>   
                        <label class="t_wrap"><span>{{scope.row.typeData.strokeWidth}}</span></label>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">  
                        <el-button type="danger" @click="delFunc(scope.row)" size="mini"></el-button>   
                        <el-button type="primary" @click="fixFunc(scope.row)" size="mini"></el-button>   
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <el-dialog
            :title="title"
            :visible.sync="visible"
            width="50%"
            center>
            <el-form :model="form" label-position="top" label-width="100px">
                <el-form-item label="key">
                    <el-input v-model="form.typeName"></el-input>
                </el-form-item>
                <el-form-item label="">    
                    <el-input-number v-model="form.typeData.strokeWidth"></el-input-number>
                </el-form-item>
                <el-form-item label="">    
                    <label class="color_set"><el-color-picker v-model="form.typeData.fill" show-alpha></el-color-picker></label>
                    <label class="color_set"><el-color-picker v-model="form.typeData.stroke" show-alpha></el-color-picker></label>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="reset"> </el-button>
                <el-button type="primary" @click="doAddFunc"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { cookie } from "@/util.js";
import ajax from "@/request.js";

export default {
    data() {
        return {
            user: "",
            visible: false,
            tableData:[],
            title: "",
            form: {
                typeId: "",
                typeName: "",
                typeData: {
                    fill:"#d5b6b6",
                    stroke:"#525252",
                    strokeWidth: "20"
                }
            }
        };
    },
    inject: ["reload"],
    created() {
        this.user = cookie.get('user');
    },
    mounted() {
        this.getData();
    },
    methods: {
        getData() {
            ajax.get("/admin/type/list", {}).then(res => {
                if (res.code == 200) {
                    this.tableData = res.data;
                }
            }).catch(err=> {
                console.log(err);
            });
        },
        reset() {
            this.visible = false;
            this.form = {
                typeId: "",
                typeName: "",
                typeData: {
                    fill:"#d5b6b6",
                    stroke:"#525252",
                    strokeWidth: "20"
                }
            };
        },
        addFunc() {
            this.visible = true;
        },
        doAddFunc() {
            if (!this.form.typeName) {
                this.$message.error("key");
            } else if (!this.form.typeData) {
                this.$message.error("");
            } else {
                if (this.title == "") {
                    ajax.post("/admin/type/add", {
                        typeName: this.form.typeName,
                        typeData: this.form.typeData
                    }).then(res => {
                        if (res.code == 200) {
                            this.$message({
                                type: 'success',
                                message: '!'
                            });
                            this.getData();
                            this.reset();
                        }
                    }); 
                } else {
                    if (!this.form.typeName) {
                        this.$message.error("key");
                    } else if (!this.form.typeData) {
                        this.$message.error("");
                    } else {
                        ajax.post("/admin/type/update", {
                            ...this.form
                        }).then(res => {
                            if (res.code == 200) {
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                                this.getData();
                                this.reset();
                            }
                        }); 
                    }
                }
                
            }
        },
        fixFunc(row) {
            this.form.typeId = row.typeId;
            this.form.typeData = row.typeData;
            this.form.typeName = row.typeName;
            this.visible = true;
            this.title = ""
        },
    
        delFunc(row) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                ajax.post("/admin/type/del?typeName=" + row.typeName, {}).then((res) => {
                    if (res.code == 200) {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                        this.getData();
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            });
        }
    }
}
</script>
<style scoped>
    .common_box{
        min-height: 300px;
    }
    .common_box .title {
        margin-bottom: 0;
    }
    .color_set{
        margin-right: 30px;
        vertical-align: middle;
    }
    .color_set .el-color-picker {
        top: 15px;
    }
    .t_wrap{
        margin-right: 10px;
    }
    .t_wrap:last-child{
        margin-right: 0;
    }
    .color_wrap{
        border: 1px solid #ddd;
        padding: 2px 3px;
        display: inline-block;
        line-height: 0;
        position: relative;
        top: 2px;
    }
    .color{
        width: 40px;
        height: 15px;
        display: inline-block;
    }
</style>
