<template>
    <el-card class="box-card common_box">
        <h3 class="title"></h3>
        <br />
        <el-alert type="success">
            <h3><strong>{{user}}</strong></h3>
        </el-alert>
        <br/>
        <div>
            <el-button @click="jump(1)"><svg-icon icon-class="upload" />&nbsp;</el-button>
            <el-button @click="jump(2)"><svg-icon icon-class="songshen" />&nbsp;</el-button>
            <el-button @click="jump(3)"><svg-icon icon-class="fenfa" />&nbsp;</el-button>
        </div>
    </el-card>
</template>

<script>
import { cookie } from "@/util.js";

export default {
    data() {
        return {
            user: ""
        };
    },
    inject: ['reload'],
    created() {
        this.user = cookie.get('user');
        if (!this.user) {
            this.$router.replace({
                path: "/login"
            });
        }
    },
    methods:{
        jump(index) {
            if (index == 1) {
                this.$router.replace({path:"/mapData"});
            } else if (index == 2) {
                this.$router.push({path:"/mapAttrs"});
            } else if (index == 3) {
                this.$router.push({path:"/colors"});
            }
        }   
    }
}
</script>
<style  scoped>
    .common_box{
        min-height: 300px;
    }
    .common_box .title {
        margin-bottom: 0;
    }
</style>
